.design-system-card.component {

    &.page-pattern {
        .example-wrapper {
            padding: 24px;

            img {
                border: solid 1px $grey;
            }

            .example {
                position: relative;

                .spot {
                    position: absolute;
                    cursor: pointer;

                    .spot-subwrapper {
                        position: relative;
                    }

                    a {
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 0;
                        z-index: 10;
                        background: $green-positive-dark;
                        color: $white;
                        width: 32px;
                        height: 32px;
                        border-radius: 50%;
                        border: solid 2px $white;
                        box-shadow: 0 0.125rem 0.375rem 0 rgba(0, 0, 0, 0.3);
                        text-align: center;
                        line-height: 1.8;
                        cursor: pointer;
                    }

                    p {
                        display: inline-block;
                        margin-left: 16px;
                        min-height: 32px;
                        background: darken($green-positive-dark, 5%);
                        color: $white;
                        padding: 1px 16px 1px 24px;
                        border-top-right-radius: 25px;
                        border-bottom-right-radius: 25px;
                        box-shadow: 0 0.125rem 0.375rem 0 rgba(0, 0, 0, 0.3);
                        display: none;
                        font-weight: 400;
                        letter-spacing: 0.04em;
                    }

                    &:hover {
                        p {
                            display: inline-block;
                        }
                    }
                }
                
                // HOTSPOT PLACEMENT - DESKTOP
                &.checklist-container {
                    #spot-01 {
                        top: 21%;
                        left: 4%;
                    }

                    #spot-02 {
                        top: 28.5%;
                        left: 4%;
                    }

                    #spot-03 {
                        top: 39.8%;
                        left: 2%;
                    }

                    #spot-04 {
                        top: 46%;
                        left: 8%;
                    }

                    #spot-05 {
                        top: 52%;
                        left: 8%;
                    }

                    #spot-06 {
                        top: 40%;
                        left: 69%;
                    }
                }
            }
        }

        .page-pattern-rules {
            padding: 24px;

            .rules-accordion-header {
                display: flex;

                h3 {
                    padding: 0;
                    margin: 0;
                    display: inline-block;
                    width: -webkit-calc(100% - 80px);
                    width: -moz-calc(100% - 80px);
                    width: calc(100% - 80px);
                }

                .rules-accordion-group-toggle {
                    .rules-accordion-group-toggle-content {
                        display: flex;
                        text-align: right;
                        width: 80px;
                        margin-top: 24px;
                        margin-bottom: 6px;
                        border-bottom: solid 2px $white;

                        &:focus {
                            outline: none;
                        }

                        h5 {
                            padding: 0;
                            margin: 0;
                            color: $grey-darker;
                            font-size: 14px;
                            margin-top: 3px;
                        }

                        .chevron {
                            margin-left: 8px;
                        }
                        
                    }
                    &:hover {
                        cursor: pointer;
                        .rules-accordion-group-toggle-content {
                            border-bottom: solid 2px $grey-darker;
                        }
                    }

                    &.open {
                        .rules-accordion-group-toggle-content .chevron {
                            transform: rotate(180deg);

                            svg {
                                margin-top: -5px;
                            }
                        }
                    }
                }
            }

            .rules-accordion-item {
                border-bottom: solid 1px $grey;

                .rules-accordion-item-tile {
                    &:focus {
                        outline: none;
                    }
                }

                .rules-accordion-item-tile-inner {
                    display: flex;
                    padding: 16px 5px;
                    margin: 0;
                    cursor: pointer;
                    
                    &:hover {
                        background: $grey-lightest;
                    }

                    h5 {
                        display: inline-block;
                        width: -webkit-calc(100% - 16px);
                        width: -moz-calc(100% - 16px);
                        width: calc(100% - 16px);
                        padding: 0;
                        margin: 0;

                        span {
                            display: inline-block;
                            background: $green-positive-dark;
                            color: $white;
                            width: 32px;
                            height: 32px;
                            border-radius: 50%;
                            border: solid 2px $white;
                            box-shadow: 0 0.125rem 0.375rem 0 rgba(0, 0, 0, 0.3);
                            text-align: center;
                            line-height: 1.8;
                            margin-right: 8px;
                        }
                        
                        &:hover {
                            text-decoration: underline; 
                        }
                    }

                    .chevron {
                        width: 16px;
                    }
                }

                .rules-accordion-item-content {
                    padding: 0 32px 0 43px;
                    display: none;
                    background: $grey-lightest;

                    p,
                    li {
                        padding-bottom: 16px;
                    }
                }

                &.open {
                    .rules-accordion-item-tile-inner {
                        background: $grey-lightest;
                        .chevron {
                            transform: rotate(180deg);
                        }
                    }
                }
            }

        }

        @media (max-width: 900px) {
            .example-wrapper .example .spot {
                a {
                    width: 28px;
                    height: 28px;
                    font-size: 13.5px;
                }

                p {
                    min-height: 27px;
                    font-size: 13.5px;
                }
            }
        }
        
        // HOTSPOT PLACEMENT - MOBILE
        .mobile-example .example.checklist-container,
        .mobile-media-query .example.checklist-container {
            #spot-01 {
                top: 5%;
                left: 4%;
            }
            #spot-02 {
                top: 17%;
                left: 4%;
            }

            #spot-03 {
                top: 41.5%;
                left: 0%;
            }

            #spot-04 {
                top: 51.5%;
                left: 18%;
            }

            #spot-05 {
                top: 65%;
                left: 18%;
            }

            #spot-06 {
                top: 93.5%;
                left: 0%;
            }
    }
    }

}